html {
  background: #000;
}

main {
  background: #fff;
}

.header {
  left: 0;
  position: absolute;
  right: 0;
  z-index: 2;
}

.home-page .h2 {
  font-weight: 800;
  line-height: 1.25em;
  max-width: 20rem;
}

.home-page .h2 a {
  text-decoration: underline;
  white-space: nowrap;
}

.home-hero {
  background: #000;
  color: #fff;
  overflow: hidden;
}

.home-hero figure {
  line-height: 0;
}

.home-hero-banner {
  align-items: flex-end;
  display: flex;
  position: relative;
}

.home-hero-banner-content {
  flex-shrink: 2;
  margin-right: -3rem;
  max-width: 27rem;
  padding-bottom: 1.5rem;
  padding-top: 9rem;
  position: relative;
  z-index: 1;
}

@media (min-width: 56em) {
  .home-hero-banner-content {
    margin-right: 0;
    padding-bottom: 3rem;
  }
}

.home-hero-links {
  align-content: baseline;
  display: flex;
  flex-wrap: wrap;
}

.home-hero-cta {
  align-items: center;
  border: 2px solid #f0c674;
  display: inline-flex;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .875rem;
  margin-right: 1.5rem;
  padding: .5rem 1.5rem;
  white-space: nowrap;
}

.home-hero-cta svg {
  margin-right: .75rem;
}

.home-hero-cta svg * {
  fill: currentColor;
}

.home-hero-cta:hover {
  background: #f0c674;
  color: #000;
}

.home-hero-v3 {
  color: #999;
  display: inline-flex;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .875rem;
  padding: .75rem 0;
  white-space: nowrap;
}

.home-hero-banner h1 {
  color: #fff;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.25em;
  max-width: none;
}

@media (min-width: 38em) {
  .home-hero-banner h1 {
    font-size: 2rem;
    line-height: 1.125em;
  }
}

@media (min-width: 56em) {
  .home-hero-banner h1 {
    font-size: 2.5rem;
  }
}

.home-hero-banner figure {
  flex-grow: 1;
  flex-shrink: 1;
  margin-right: -5rem;
  padding-top: 6rem;
  width: 66.6666666667%;
  z-index: 0;
}

@media (min-width: 56em) {
  .home-hero-banner figure {
    margin-bottom: -1.5rem;
    margin-right: 0;
    width: auto;
  }
}

.home-hero-screenshot {
  display: none;
  position: relative;
}

@media (min-width: 56em) {
  .home-hero-screenshot {
    display: block;
  }
}

.home-blueprints {
  overflow: hidden;
  padding-bottom: 0;
  padding-top: 3rem;
}

@media (min-width: 38em) {
  .home-blueprints {
    padding-top: 7.5rem;
  }
}

.home-blueprints .h2 {
  position: relative;
  z-index: 1;
}

@media (min-width: 38em) {
  .home-blueprints .h2 {
    position: absolute;
  }
}

.home-blueprints figure {
  line-height: 0;
  margin: -3rem -6rem 0;
}

.home-panel {
  overflow: hidden;
}

.home-panel-container {
  background: #313741;
  padding: 1.5rem 1.5rem 0;
  position: relative;
}

@media (min-width: 56em) {
  .home-panel-container {
    padding: 3rem 3rem 1.5rem;
  }
}

@media (min-width: 75em) {
  .home-panel-container {
    margin: 0 -3rem;
  }
}

.home-panel-container > * {
  position: relative;
  z-index: 1;
}

.home-panel-gallery {
  display: none;
  margin-bottom: 3rem;
}

@media (min-width: 38em) {
  .home-panel-gallery {
    display: block;
  }
}

.home-panel-gallery figure {
  background: #000;
  border-radius: 6px;
  line-height: 0;
}

.home-panel-gallery figure:not(:first-child) {
  display: none;
}

.home-panel-gallery-links {
  color: #c5c9c6;
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.5rem;
}

.home-panel-gallery-links > * {
  flex-grow: 0;
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
}

@media (min-width: 38em) {
  .home-panel-gallery-links > * {
    width: 33.3333333333%;
  }
}

@media (min-width: 56em) {
  .home-panel-gallery-links > * {
    width: 20%;
  }
}

.home-panel-gallery-links a {
  display: block;
}

.home-panel-gallery-links span {
  background: #262b33;
  display: block;
  line-height: 0;
  margin: 0 auto 1rem;
  overflow: hidden;
}

@media (min-width: 38em) {
  .home-panel-gallery-links a[aria-current] span {
    outline: 2px solid #f0c674;
    outline-offset: 2px;
  }

  .home-panel-gallery-links a:not([aria-current]) img {
    -webkit-filter: blur(2px);
            filter: blur(2px);
    opacity: .3;
    transition: all .3s;
  }

  .home-panel-gallery-links figure:hover img {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
  }

  .home-panel-gallery-links figcaption p {
    color: rgba(255, 255, 255, 0.75);
    font-size: .875rem;
  }
}

.home-plugins {
  padding: 3rem 0;
}

@media (min-width: 56em) {
  .home-plugins {
    padding: 6rem 0;
  }
}

@media (min-width: 56em) {
  .home-plugins .wrap {
    display: flex;
  }
}

.home-plugins header {
  flex-basis: 33.3333333333%;
  flex-grow: 1;
  margin-right: 1.5rem;
}

.home-plugins .h2 {
  max-width: none;
}

.home-plugins .description {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .75rem;
  margin-bottom: 3rem;
}

.home-plugins .description a {
  text-decoration: underline;
}

.home-plugins figure {
  flex-basis: 66.6666666667%;
  flex-grow: 2;
  margin-top: .25rem;
}

.home-highlights-grid {
  border-bottom: 4px solid #efefef;
  padding: 3rem 0;
}

@media (min-width: 56em) {
  .home-highlights-grid {
    padding: 4.5rem 0;
  }
}

.home-highlights-grid > * {
  min-width: 0;
}

.home-highlights-grid > * + * {
  margin-top: 3rem;
}

@supports (display: grid) {
  .home-highlights-grid {
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: 1fr;
  }

@media (min-width: 56em) {
    .home-highlights-grid {
      grid-template-columns: 1fr 1fr;
    }
}

  .home-highlights-grid > * + * {
    margin-top: 0;
  }
}

.home-highlights-features li {
  align-items: baseline;
  color: rgba(60, 67, 80, 0.75);
  display: flex;
  font-size: .875rem;
  line-height: 1.5;
}

.home-highlights-features svg {
  margin-right: .5rem;
  position: relative;
  top: .175rem;
}

.home-highlights-features svg * {
  fill: #cca000;
}

.home-features figure {
  margin-bottom: .5rem;
}

.home-features figure svg * {
  stroke: #cca000;
}

.home-features li {
  margin-bottom: 1.5rem;
}

@supports (display: grid) {
  .home-features ul {
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  }

  .home-features li {
    margin-bottom: 0;
  }
}

.home-features li h3 {
  color: #000;
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 1.5;
  margin-bottom: 0;
}

.home-features li .description {
  font-size: .875rem;
}

.home-features-more {
  border-top: 4px solid #efefef;
  display: block;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .875rem;
  margin-top: 4.5rem;
  padding-top: 1.5rem;
  text-align: left;
}

.home-voices .h2 {
  margin-bottom: 3rem;
  max-width: none;
  text-align: center;
}

.home-actions {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 6rem 1.5rem 9rem;
}

@media (min-width: 38em) {
  .home-actions {
    flex-direction: row;
  }
}

.home-actions a {
  align-items: center;
  background: #000;
  border-radius: 1px;
  color: #fff;
  display: flex;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .875rem;
  font-weight: 400;
  margin: 0 1.5rem;
  padding: .5rem 1.5rem;
}

@media (max-width: 37.9375em) {
  .home-actions a {
    margin-bottom: 1.5rem;
    width: 100%;
  }
}

.home-actions a svg {
  margin-right: .75rem;
  width: 1rem;
}

.home-actions a svg * {
  fill: #f0c674;
}

.home-actions a:hover {
  background: #f0c674;
  color: #000;
}

.home-actions a:hover svg * {
  fill: #000;
}

.home-kosmos {
  background: #000;
  color: #fff;
}

.home-kosmos-form {
  padding: 3rem 0;
}

@media (min-width: 38em) {
  .home-kosmos-form {
    border-bottom: 4px solid #3c4350;
  }
}

@media (min-width: 56em) {
  .home-kosmos-form {
    padding: 6rem 0;
  }
}

.home-kosmos-form .h2 {
  color: #fff;
  font-weight: 600;
}

.home-kosmos-form label {
  color: #f0c674;
}

/* -----  Newsletter  ------------------------------------------------------------- */

.footer {
  background: #000 !important;
}

.footer-layout {
  padding-top: 6rem;
}

